// Single side border-radius

// Helper function to replace negative values with 0
@function valid-radius(Rsradius) {
  Rsreturn: ();
  @each Rsvalue in Rsradius {
    @if type-of(Rsvalue) == number {
      Rsreturn: append(Rsreturn, max(Rsvalue, 0));
    } @else {
      Rsreturn: append(Rsreturn, Rsvalue);
    }
  }
  @return Rsreturn;
}

// scss-docs-start border-radius-mixins
@mixin border-radius(Rsradius: Rsborder-radius, Rsfallback-border-radius: false) {
  @if Rsenable-rounded {
    border-radius: valid-radius(Rsradius);
  }
  @else if Rsfallback-border-radius != false {
    border-radius: Rsfallback-border-radius;
  }
}

@mixin border-top-radius(Rsradius: Rsborder-radius) {
  @if Rsenable-rounded {
    border-top-left-radius: valid-radius(Rsradius);
    border-top-right-radius: valid-radius(Rsradius);
  }
}

@mixin border-end-radius(Rsradius: Rsborder-radius) {
  @if Rsenable-rounded {
    border-top-right-radius: valid-radius(Rsradius);
