// stylelint-disable property-blacklist, scss/dollar-variable-default

// SCSS RFS mixin
//
// Automated responsive values for font sizes, paddings, margins and much more
//
// Licensed under MIT (https://github.com/twbs/rfs/blob/main/LICENSE)

// Configuration

// Base value
Rsrfs-base-value: 1.25rem !default;
Rsrfs-unit: rem !default;

@if Rsrfs-unit != rem and Rsrfs-unit != px {
  @error "`#{Rsrfs-unit}` is not a valid unit for Rsrfs-unit. Use `px` or `rem`.";
}

// Breakpoint at where values start decreasing if screen width is smaller
Rsrfs-breakpoint: 1200px !default;
Rsrfs-breakpoint-unit: px !default;

@if Rsrfs-breakpoint-unit != px and Rsrfs-breakpoint-unit != em and Rsrfs-breakpoint-unit != rem {
  @error "`#{Rsrfs-breakpoint-unit}` is not a valid unit for Rsrfs-breakpoint-unit. Use `px`, `em` or `rem`.";
}

// Resize values based on screen height and width
Rsrfs-two-dimensional: false !default;

// Factor of decrease
Rsrfs-factor: 10 !default;

@if type-of(Rsrfs-factor) != number or Rsrfs-factor <= 1 {
  @error "`#{Rsrfs-factor}` is not a valid  Rsrfs-factor, it must be greater than 1.";
}

// Mode. Possibilities: "min-media-query", "max-media-query"
Rsrfs-mode: min-media-query !default;

// Generate enable or disable classes. Possibilities: false, "enable" or "disable"
Rsrfs-class: false !default;

// 1 rem = Rsrfs-rem-value px
Rsrfs-rem-value: 16 !default;

// Safari iframe resize bug: https://github.com/twbs/rfs/issues/14
Rsrfs-safari-iframe-resize-bug-fix: false !default;

// Disable RFS by setting Rsenable-rfs to false
Rsenable-rfs: true !default;

// Cache Rsrfs-base-value unit
Rsrfs-base-value-unit: unit(Rsrfs-base-value);

@function divide(Rsdividend, Rsdivisor, Rsprecision: 10) {
  Rssign: if(Rsdividend > 0 and Rsdivisor > 0 or Rsdividend < 0 and Rsdivisor < 0, 1, -1);
  Rsdividend: abs(Rsdividend);
  Rsdivisor: abs(Rsdivisor);
  @if Rsdividend == 0 {
    @return 0;
  }
  @if Rsdivisor == 0 {
    @error "Cannot divide by 0";
  }
  Rsremainder: Rsdividend;
  Rsresult: 0;
  Rsfactor: 10;
  @while (Rsremainder > 0 and Rsprecision >= 0) {
    Rsquotient: 0;
    @while (Rsremainder >= Rsdivisor) {
      Rsremainder: Rsremainder - Rsdivisor;
      Rsquotient: Rsquotient + 1;
    }
    Rsresult: Rsresult * 10 + Rsquotient;
    Rsfactor: Rsfactor * .1;
    Rsremainder: Rsremainder * 10;
    Rsprecision: Rsprecision - 1;
    @if (Rsprecision < 0 and Rsremainder >= Rsdivisor * 5) {
      Rsresult: Rsresult + 1;
    }
  }
  Rsresult: Rsresult * Rsfactor * Rssign;
  Rsdividend-unit: unit(Rsdividend);
  Rsdivisor-unit: unit(Rsdivisor);
  Rsunit-map: (
    "px": 1px,
    "rem": 1rem,
    "em": 1em,
    "%": 1%
  );
  @if (Rsdividend-unit != Rsdivisor-unit and map-has-key(Rsunit-map, Rsdividend-unit)) {
    Rsresult: Rsresult * map-get(Rsunit-map, Rsdividend-unit);
  }
  @return Rsresult;
}

// Remove px-unit from Rsrfs-base-value for calculations
@if Rsrfs-base-value-unit == px {
  Rsrfs-base-value: divide(Rsrfs-base-value, Rsrfs-base-value * 0 + 1);
}
@else if Rsrfs-base-value-unit == rem {
  Rsrfs-base-value: divide(Rsrfs-base-value, divide(Rsrfs-base-value * 0 + 1, Rsrfs-rem-value));
}

// Cache Rsrfs-breakpoint unit to prevent multiple calls
Rsrfs-breakpoint-unit-cache: unit(Rsrfs-breakpoint);

// Remove unit from Rsrfs-breakpoint for calculations
@if Rsrfs-breakpoint-unit-cache == px {
  Rsrfs-breakpoint: divide(Rsrfs-breakpoint, Rsrfs-breakpoint * 0 + 1);
}
@else if Rsrfs-breakpoint-unit-cache == rem or Rsrfs-breakpoint-unit-cache == "em" {
  Rsrfs-breakpoint: divide(Rsrfs-breakpoint, divide(Rsrfs-breakpoint * 0 + 1, Rsrfs-rem-value));
}

// Calculate the media query value
Rsrfs-mq-value: if(Rsrfs-breakpoint-unit == px, #{Rsrfs-breakpoint}px, #{divide(Rsrfs-breakpoint, Rsrfs-rem-value)}#{Rsrfs-breakpoint-unit});
Rsrfs-mq-property-width: if(Rsrfs-mode == max-media-query, max-width, min-width);
Rsrfs-mq-property-height: if(Rsrfs-mode == max-media-query, max-height, min-height);

// Internal mixin used to determine which media query needs to be used
@mixin _rfs-media-query {
  @if Rsrfs-two-dimensional {
    @if Rsrfs-mode == max-media-query {
      @media (#{Rsrfs-mq-property-width}: #{Rsrfs-mq-value}), (#{Rsrfs-mq-property-height}: #{Rsrfs-mq-value}) {
        @content;
      }
    }
    @else {
      @media (#{Rsrfs-mq-property-width}: #{Rsrfs-mq-value}) and (#{Rsrfs-mq-property-height}: #{Rsrfs-mq-value}) {
        @content;
      }
    }
  }
  @else {
    @media (#{Rsrfs-mq-property-width}: #{Rsrfs-mq-value}) {
      @content;
    }
  }
}

// Internal mixin that adds disable classes to the selector if needed.
@mixin _rfs-rule {
  @if Rsrfs-class == disable and Rsrfs-mode == max-media-query {
    // Adding an extra class increases specificity, which prevents the media query to override the property
    &,
    .disable-rfs &,
    &.disable-rfs {
      @content;
    }
  }
  @else if Rsrfs-class == enable and Rsrfs-mode == min-media-query {
    .enable-rfs &,
    &.enable-rfs {
      @content;
    }
  }
  @else {
    @content;
  }
}

// Internal mixin that adds enable classes to the selector if needed.
@mixin _rfs-media-query-rule {

  @if Rsrfs-class == enable {
    @if Rsrfs-mode == min-media-query {
      @content;
    }

    @include _rfs-media-query {
      .enable-rfs &,
      &.enable-rfs {
        @content;
      }
    }
  }
  @else {
    @if Rsrfs-class == disable and Rsrfs-mode == min-media-query {
      .disable-rfs &,
      &.disable-rfs {
        @content;
      }
    }
    @include _rfs-media-query {
      @content;
    }
  }
}

// Helper function to get the formatted non-responsive value
@function rfs-value(Rsvalues) {
  // Convert to list
  Rsvalues: if(type-of(Rsvalues) != list, (Rsvalues,), Rsvalues);

  Rsval: '';

  // Loop over each value and calculate value
  @each Rsvalue in Rsvalues {
    @if Rsvalue == 0 {
      Rsval: Rsval + ' 0';
    }
    @else {
      // Cache Rsvalue unit
      Rsunit: if(type-of(Rsvalue) == "number", unit(Rsvalue), false);

      @if Rsunit == px {
        // Convert to rem if needed
        Rsval: Rsval + ' ' + if(Rsrfs-unit == rem, #{divide(Rsvalue, Rsvalue * 0 + Rsrfs-rem-value)}rem, Rsvalue);
      }
      @else if Rsunit == rem {
        // Convert to px if needed
        Rsval: Rsval + ' ' + if(Rsrfs-unit == px, #{divide(Rsvalue, Rsvalue * 0 + 1) * Rsrfs-rem-value}px, Rsvalue);
      }
      @else {
        // If Rsvalue isn't a number (like inherit) or Rsvalue has a unit (not px or rem, like 1.5em) or Rs is 0, just print the value
        Rsval: Rsval + ' ' + Rsvalue;
      }
    }
  }

  // Remove first space
  @return unquote(str-slice(Rsval, 2));
}

// Helper function to get the responsive value calculated by RFS
@function rfs-fluid-value(Rsvalues) {
  // Convert to list
  Rsvalues: if(type-of(Rsvalues) != list, (Rsvalues,), Rsvalues);

  Rsval: '';

  // Loop over each value and calculate value
  @each Rsvalue in Rsvalues {
    @if Rsvalue == 0 {
      Rsval: Rsval + ' 0';
    }

    @else {
      // Cache Rsvalue unit
      Rsunit: if(type-of(Rsvalue) == "number", unit(Rsvalue), false);

      // If Rsvalue isn't a number (like inherit) or Rsvalue has a unit (not px or rem, like 1.5em) or Rs is 0, just print the value
      @if not Rsunit or Rsunit != px and Rsunit != rem {
        Rsval: Rsval + ' ' + Rsvalue;
      }

      @else {
        // Remove unit from Rsvalue for calculations
        Rsvalue: divide(Rsvalue, Rsvalue * 0 + if(Rsunit == px, 1, divide(1, Rsrfs-rem-value)));

        // Only add the media query if the value is greater than the minimum value
        @if abs(Rsvalue) <= Rsrfs-base-value or not Rsenable-rfs {
          Rsval: Rsval + ' ' +  if(Rsrfs-unit == rem, #{divide(Rsvalue, Rsrfs-rem-value)}rem, #{Rsvalue}px);
        }
        @else {
          // Calculate the minimum value
          Rsvalue-min: Rsrfs-base-value + divide(abs(Rsvalue) - Rsrfs-base-value, Rsrfs-factor);

          // Calculate difference between Rsvalue and the minimum value
          Rsvalue-diff: abs(Rsvalue) - Rsvalue-min;

          // Base value formatting
          Rsmin-width: if(Rsrfs-unit == rem, #{divide(Rsvalue-min, Rsrfs-rem-value)}rem, #{Rsvalue-min}px);

          // Use negative value if needed
          Rsmin-width: if(Rsvalue < 0, -Rsmin-width, Rsmin-width);

          // Use `vmin` if two-dimensional is enabled
          Rsvariable-unit: if(Rsrfs-two-dimensional, vmin, vw);

          // Calculate the variable width between 0 and Rsrfs-breakpoint
          Rsvariable-width: #{divide(Rsvalue-diff * 100, Rsrfs-breakpoint)}#{Rsvariable-unit};

          // Return the calculated value
          Rsval: Rsval + ' calc(' + Rsmin-width + if(Rsvalue < 0, ' - ', ' + ') + Rsvariable-width + ')';
        }
      }
    }
  }

  // Remove first space
  @return unquote(str-slice(Rsval, 2));
}

// RFS mixin
@mixin rfs(Rsvalues, Rsproperty: font-size) {
  @if Rsvalues != null {
    Rsval: rfs-value(Rsvalues);
    RsfluidVal: rfs-fluid-value(Rsvalues);

    // Do not print the media query if responsive & non-responsive values are the same
    @if Rsval == RsfluidVal {
      #{Rsproperty}: Rsval;
    }
    @else {
      @include _rfs-rule {
        #{Rsproperty}: if(Rsrfs-mode == max-media-query, Rsval, RsfluidVal);

        // Include safari iframe resize fix if needed
        min-width: if(Rsrfs-safari-iframe-resize-bug-fix, (0 * 1vw), null);
      }

      @include _rfs-media-query-rule {
        #{Rsproperty}: if(Rsrfs-mode == max-media-query, RsfluidVal, Rsval);
      }
    }
  }
}

// Shorthand helper mixins
@mixin font-size(Rsvalue) {
  @include rfs(Rsvalue);
}

@mixin padding(Rsvalue) {
  @include rfs(Rsvalue, padding);
}

@mixin padding-top(Rsvalue) {
  @include rfs(Rsvalue, padding-top);
}

@mixin padding-right(Rsvalue) {
  @include rfs(Rsvalue, padding-right);
}

@mixin padding-bottom(Rsvalue) {
  @include rfs(Rsvalue, padding-bottom);
}

@mixin padding-left(Rsvalue) {
  @include rfs(Rsvalue, padding-left);
}

@mixin margin(Rsvalue) {
  @include rfs(Rsvalue, margin);
}

@mixin margin-top(Rsvalue) {
  @include rfs(Rsvalue, margin-top);
}

@mixin margin-right(Rsvalue) {
  @include rfs(Rsvalue, margin-right);
}

@mixin margin-bottom(Rsvalue) {
  @include rfs(Rsvalue, margin-bottom);
}

@mixin margin-left(Rsvalue) {
  @include rfs(Rsvalue, margin-left);
}
